.nav ul {
  margin: 0;
  padding: 0;
  display: grid;
  gap: 34px;
  grid-auto-flow: column;

  @media (max-width: $tablet) {
    gap: 24px;
  }

  @media (max-width: $mobile) {
    gap: 13px;
    grid-auto-flow: inherit;
  }
}

.nav li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav a {
  display: block;
  color: var(--default-white-color);
  line-height: 20px;
  font-weight: 500;
  padding: 5px 0;

  &:hover {
    opacity: 0.5;
  }

  @media (max-width: $tablet) {
    font-size: 12px;
    line-height: 15px;
  }
}

.nav--opened a {
  @media (max-width: $mobile) {
    color: var(--default-font-color);
  }
}

.nav__toggle {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  width: 50px;
  height: 50px;
  border: 0;
  background: none;

  @media (max-width: $mobile) {
    display: block;
  }
}

.nav--opened ul {
  @media (max-width: $mobile) {
    background-color: var(--default-white-color);
    position: absolute;
    top: 0;
    z-index: 2;
    right: 0;
    padding: 60px 30px 40px 15px;
    min-width: 285px;
    width: calc(100% - 35px);
    list-style: none;
    min-height: 100%;
    grid-auto-rows: min-content;
  }
}

.nav--nojs ul {
  @media (max-width: $mobile) {
    background-color: var(--default-white-color);
    position: absolute;
    top: 0;
    z-index: 2;
    right: 0;
    flex-direction: column;
    padding: 60px 30px 40px 15px;
    min-width: 285px;
    width: 100%;
    list-style: none;
  }
}

.nav--nojs a {
  @media (max-width: $mobile) {
    color: var(--default-font-color);
  }
}

.nav--opened .nav__toggle {
  @media (max-width: $mobile) {
    z-index: 3;
    background: none;
    display: block;

    &::before,
    &::after {
      top: 25px;
      right: 15px;
      content: "";
      position: absolute;
      width: 15px;
      height: 2px;
      background-color: var(--default-font-color);
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }
}

.nav__logo-wrapper_blue {
  display: none;
}

.nav__logo-wrapper_blue--shown {
  @media (max-width: $mobile) {
    position: absolute;
    z-index: 3;
    left: 52px;
    top: 14px;
  }
}


.nav--opened,
.nav--nojs .nav__logo-wrapper_blue {
  @media (max-width: $mobile) {
    display: block;
  }
}

.nav--closed ul {
  @media (max-width: $mobile) {
    display: none;
  }
}

.nav--opened .nav__logo-wrapper_blue {
  @media (max-width: $mobile) {
    display: block;
  }
}

.nav--closed .nav__toggle {
  @media (max-width: $mobile) {
    &::before {
      content: "";
      position: absolute;
      top: 25px;
      left: 11px;
      width: 24px;
      height: 2px;
      background-color: var(--default-white-color);
      box-shadow: 0 6px 0 0 var(--default-white-color), 0 12px 0 0 var(--default-white-color);
    }
  }
}
